* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

html {
  --nav-height: 60px;
  --blue-text-color: #00acdc;
  margin: 0;
}

body {
  text-transform: uppercase;
  background-color: beige;
  margin: 0;
}

.container {
  text-align: center;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__menu {
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  height: var(--nav-height);
  font-size: 12px;
  line-height: var(--nav-height);
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link-1, .nav__link:hover {
  color: var(--blue-text-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
  left: 0;
}
